* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  outline: 0;
  list-style: none;
  text-decoration: none;
}
:root {
  --primary-hue: 270;
  --black-lightness: 15%;
  --dark-lightness: 44%;
  --light-lightness: 94%;
  --white-lightness: 100%;
  --color-primary: hsl(var(--primary-hue), 89%, 41%);
  --color-white: hsl(0, 5%, var(--white-lightness));
  --color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
  --color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
  --color-dark: hsl(var(--primary-hue), 5%, var(--dark-lightness));
  --color-secondary: #ff521c;
  --color-lighter: #edf1fd;
  --bg-gradient: linear-gradient(to bottom right, rgb(19, 17, 17), black, grey);

  /* GENERAL CONTAINER VARIABLES */
  --container-width-1g: 76%;
  --container-width-md: 90%;
  /* BORDER RADIUS VARIABLES */
  --border-radius-1: 1.2rem;
  --border-radius-2: 0.8rem;
  --border-radius-3: 0.5rem;
  /* GENERAL TRANSITION VARIABLE */
  --transition: all 500ms ease;
}
.light {
  --color-secondary: #ff521c;
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: "Great Vibes", cursive;
  font-family: "Lato", sans-serif;
  line-height: 1.5rem;
  font-size: 0.9rem;
  overflow-x: hidden !important;
}

/* general container class */
.container {
  width: var(--container-width-1g);
  margin-inline: auto;
  max-width: 1920px;
}
/* general section styles */
section {
  padding: 10rem 0;
  background: linear-gradient(to bottom right, rgb(19, 17, 17), black, grey);
  transition: var(--transition);
}
/* section:nth-child(even) {
  background: var(--color-white);
} */
section > h2 {
  text-align: center;
  color: var(--color-secondary);
}
section > p {
  text-align: center;
  margin: 0.5 rem auto 4rem;
  width: 40%;
}

/* general section styles */
a {
  color: var(--color-white);
  font-weight: bold;
  font-size: 1.2rem;
  transition: var(--transition);
}

/* general heading styles */

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.3;
  color: var(--color-black);
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 2.6rem;
  color: var(--color-white);
}
h4 {
  font-size: 1.1rem;
}
h5 {
  font-size: 0.95rem;
}
h6 {
  font-size: 0.8rem;
}

/* general image styles */
img {
  display: block;
  object-fit: cover;
  width: 100%;
}

/* general button styles */
.btn {
  display: inline-block;
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 1rem;
  background: var(--color-light);
  color: var(--color-black);
  padding: 0.6rem 2.5rem;
  border: 2px solid transparent;
  border-radius: var(--border-radius-2);
  cursor: pointer;
}
.btn:hover {
  background-color: transparent;
  border-color: var(--color-light);
  transform: translateY(-0.5rem);
}
.btn.white {
  background: var(--color-white);
}
.btn.white:hover {
  background: transparent;
  border-color: var(--color-secondary);
  color: var(--color-secondary);
}
.btn.primary {
  background: var(--color-secondary);
  color: white;
}
.btn.primary:hover {
  background: transparent;
  border-color: var(--color-secondary);
  color: var(--color-secondary);
}
.btn.sm {
  padding: 0.5rem 1.2rem;
}
.Port {
  position: relative;
}
