header {
  height: calc(100vh);
  background: var(--color-lighter);
  transition: var(--transition);
  background: linear-gradient(to bottom right, rgb(19, 17, 17), black, grey);
  display: flex;
  justify-content: center;
  align-items: center;
}
.header_container {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding: 2rem;
}
.header_profile {
  width: 18rem;
  height: 22rem;
  border-radius: 9rem 9rem 2rem 2rem;
  overflow: hidden;
  margin-bottom: 3rem;
}
.header_container p {
  width: 35rem;
  text-align: center;
  margin: 0.6rem 0 2rem;
  color: var(--color-white);
  font-size: 1.3rem;
  line-height: 2rem;
}
.header_cta {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}
.header_socials {
  position: absolute;
  left: 0;
  bottom: 30%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.header_socials a {
  width: 2.4rem;
  aspect-ratio: 1/1;
  background: var(--color-light);
  color: var(--color-black);
  display: grid;
  place-items: center;
  font-weight: bold;
  border-radius: var(--border-radius-3);
}
.header_socials a:hover {
  background: var(--color-secondary);
  color: var(--color-white);
  transform: translateX(-0.5rem);
  border: 2px solid;
}

@media screen and (max-width: 1024px) {
  header {
    margin-top: 0;
    height: 100vh;
  }
  .header_container {
    width: 94% !important;
    padding: 2.5rem 0.5rem;
  }
  .header_container p {
    width: 28rem;
    text-align: center;
    margin: 0.6rem 0 2rem;
    font-size: 1.1rem;
    line-height: 1.3rem;
  }
}
@media screen and (max-width: 600px) {
  .header_container {
    padding: 3rem 0.5rem;
  }
  .header_profile {
    width: 14rem;
    height: 17rem;
    max-width: 60%;
    max-height: 35vh;
  }
  .header_container p {
    width: 100%;
    font-size: 0.9rem;
    line-height: 1rem;
  }
  .header_cta {
    gap: 1rem;
  }
  .header_socials {
    display: none;
  }
}
