.footer {
  background: var(--color-secondary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  /* margin-top: 4rem; */
}
footer a {
  color: var(--color-white);
}

.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}
.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}
.footer_socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}
.footer_socials a {
  background: var(--color-black);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border-radius: 0.7rem;
  border: 1px solid transparent;
}
.footer_socials a :hover {
  border: 1px solid transparent;
}
.footer_copy {
  color: var(--color-white);
  margin-bottom: 4rem;
}

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
  }
  .footer_socials {
    margin-bottom: 2rem;
  }
}
