.about_container {
  display: grid;
  grid-template-columns: 35% 53%;
  gap: 12%;
}
.about_portrait {
  height: fit-content;
  max-height: 40rem;
  border: 1rem solid #10151d;
  transition: var(--transition);
}
.about_portrait:hover {
  border-width: 0;
}
.about_cards {
  margin: 3rem 0 1.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}
.card.about_card {
  text-align: center;
  background: #10151d;
  padding: 0.4rem;
  border-radius: var(--border-radius-3);
  color: white;
}
.about_card_icon {
  font-size: 2.3rem;
  color: var(--color-secondary);
  border-radius: var(--border-radius-3);
  width: fit-content;
  margin-inline: auto;
  padding: 0.6rem;
  display: flex;
  align-items: center;
  transition: var(--transition);
}
.card.about_card:hover .about_card_icon {
  background: var(--color-secondary);
  color: var(--color-white);
}
.card.about_card h5 {
  margin: 1rem 0;
}
.right > p {
  margin-top: 1.5rem;
  margin-bottom: 0.8rem;
  color: var(--color-white) !important;
  font-size: 1.2rem;
  text-align: justify;
}
.right > .btn {
  margin-top: 2.5rem;
}

@media screen and (max-width: 1024px) {
  .left {
    display: none;
  }
  .about_container {
    grid-template-columns: 1fr;
  }
  .about_container h2 {
    text-align: center;
  }
  .right > p {
    margin-top: 1rem;
    font-size: 1rem;
  }
}

@media screen and (max-width: 600px) {
  .right > .btn {
    margin-top: 2rem;
  }
  .right > p {
    margin-bottom: 0.8rem;
  }
  .about_container {
    width: 95% !important;
    padding: 0.8rem;
  }
  .right > p {
    margin-top: 0.7rem;
    font-size: 1rem;
  }
}
