nav {
  width: 100vw;
  height: 5rem;
  background: #0a0a0a;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
  transition: var(--transition);
  position: fixed !important;
  top: 0 !important;
  left: 0;
}

.nav_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav_logo {
  width: 2.5rem;
  border-radius: 50%;
  overflow: hidden;
}
.nav_menu {
  display: flex;
  gap: 3.5rem;
  background: #0a0a0a;
  justify-content: center;
  align-items: center;
}
#theme_icon {
  background: transparent;
  color: black;
  font-size: 2rem;
  cursor: pointer;
}

.Bars {
  display: none;
}

@media screen and (max-width: 1024px) {
  nav {
    position: fixed;
    top: 0;
    left: 0;
  }
  .Bars {
    display: block;
    color: white;
    font-size: 2rem;
    cursor: pointer;
  }
  .Resume {
    display: none;
  }
  .nav_menu {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0px;
    top: 5rem;
    width: 100vw;
    height: calc(65vh - 5rem);
    align-items: center;
    z-index: 99999;
    padding: 2rem 1rem;
    gap: 0;
  }
  .nav_menu.hide_lists {
    display: none;
  }

  .nav_menu li {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 1rem 2rem;
  }
  .nav_menu li:hover {
    background: var(--color-secondary);
    cursor: pointer;
  }
}
@media screen and (max-width: 600px) {
  nav {
    position: fixed;
    top: 0;
    left: 0;
  }
  .Resume {
    display: none;
  }
  .Bars {
    display: block;
    color: white;
    font-size: 2rem;
    cursor: pointer;
  }
  .nav_menu {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0px;
    top: 5rem;
    width: 100vw;
    height: calc(100vh - 5rem);
    align-items: center;
    z-index: 99999;
    padding: 1rem 1rem;
    gap: 0;
  }
  .nav_menu li {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 1rem 2rem;
  }
  .nav_menu li:hover {
    background: var(--color-secondary);
    cursor: pointer;
  }
}
