.portfolio_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
  width: 95% !important;

  padding: 1rem;
}
.portfolio_item {
  background: #1c1f2d;
  padding: 1.3rem;
  border-radius: var(--border-radius-1);
  border: 1px solid transparent;
  transition: var(--transition);
}
.portfolio_item:hover {
  border: 1px solid var(--color-secondary);
  background: transparent;
}
.portfoli_item_image {
  border-radius: var(--border-radius-2);
  overflow: hidden;
}
.portfolio_item h3 {
  padding: 1rem 0;
}
.portfolio_item_cta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

.portfolio_item_desc > div {
  padding: 1rem 0;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 0.5rem;
}
.portfolio_item_desc span {
  padding: 0.3rem 0.5rem;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  background: var(--color-secondary);
}
.portfolio_item_desc p {
  color: white;
  font-size: 1rem;
}
.Projects {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

@media screen and (max-width: 1024px) {
  .portfolio_container {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
@media screen and (max-width: 750px) {
  .portfolio_container {
    grid-template-columns: 1fr;
    gap: 1.2rem;
  }
  .portfolio_item_desc span {
    padding: 0.2rem 0.5rem;
  }
}
